export default {
  props: {
    show: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      columns: [
        "宁波地区",
        "杭州地区",
        "温州地区",
        "绍兴地区",
        "湖州地区",
        "嘉兴地区",
        "金华地区",
      ],
    };
  },
  methods: {
    onConfirm(e) {
      this.$emit("confirm", e);
    },
    onCancel(e) {
      this.$emit("update:show", false);
    },
  },
};
