import SelectCity from "@/components/SelectCity/index";
import TopHeader from "./components/TopHeader/index";
import DebtTotal from "./components/DebtTotal/index";
import TrendTotal from "./components/TrendTotal/index";
import ProfitTotal from "./components/ProfitTotal/index";
import OldTotal from "./components/OldTotal/index";
import CostTotal from "./components/CostTotal/index";
import SalesGrossProfitTotal from "./components/SalesGrossProfitTotal/index";
import CategoryGrossProfitTotal from "./components/CategoryGrossProfitTotal/index";

export default {
  components: {
    SelectCity,
    TopHeader,
    DebtTotal,
    TrendTotal,
    ProfitTotal,
    OldTotal,
    CostTotal,
    SalesGrossProfitTotal,
    CategoryGrossProfitTotal,
  },
  name: "Boss",
  data() {
    return {
      city: '成都地区',
      showPicker: false,
    };
  },
  methods: {
    onCityConfirm(city) {
      this.city = city
      this.showPicker = false
    },
    onCityOpen() {
      this.showPicker = true;
    },
    onDateOpen() {},
  },
};
